import React, { Component } from 'react';
import {Link} from 'react-router-dom';
import { Icon, NumberCount } from '../home02/about/index';


class About extends Component {
    render() {
        return (
            <div className="tf-modal-content">
                <section className="about-me t2 s3 shape-am position-relative" id="about">
                    <Icon />                  
                    <div className="container">
                        <div className="row position-relative">
                            <div className="col-lg-7 animate-element wow delay5 fadeInDown" data-wow-delay="0.5s">
                                <div className="flat-title t2">
                                <h4 className="sub-title mg-b22">About Me</h4>
                                <h2 className="title-section color-d12 mg-b41">Personal Info</h2>

                                <div className="description">
                                    <p className="lt-sp01">
                                        이름: 정재승
                                    </p>
                                    <p className="lt-sp01">
                                        생년월일: 1991.01.13
                                    </p>
                                    <p className="lt-sp01">
                                        병역: 육군 병장 전역
                                    </p>
                                 
                                    <p className="lt-sp01">
                                        연락처: 010-7330-8180
                                    </p>
                                    <p className="lt-sp01">
                                        이메일: xiel0113@naver.com
                                    </p>
                                </div>
                                </div>
                            </div>
                            <div className="fact">
                                <NumberCount />
                                <div className="btn-contact bg-s1 text-center">
                                    <h4 className="title color-d12">email</h4>
                                    <Link to="#" className="email color-s1 color-d14">xiel0113@naver.com</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>    
        );
    }
}

export default About;
