import React, { Component } from 'react';
import {Link} from 'react-router-dom';
class CaseStudy extends Component {
    render() {
        return (
            <div className="tf-modal-content">
                <section className="flat-case-study s3" id="portfolio">
                    <div className="container">
                        <div className="d-md-flex">
                            <div className="col-left animate-element wow delay5 fadeInDown" data-wow-delay="0.5s">
                                <div className="image-link t1">
                                    <div className="featured-post">

                        <a href="https://github.com/JaeseungJung/who--cat/tree/main/client/Vue/files"> <img src="images/section/whocat.png" alt="images" /></a>
                                    </div>
                                    <div className="text-center">
                            <h1 className="clip is-full-width title color-d2 mg-b31">
                                   누구냥 - 반려묘 객체 인증 시스템 </h1>
                                   개발기간: 3주
                                    </div>
                                    Vue를 이용해 처음 만든 웹입니다.
                                    고양이의 비문을 등록하여 관리하는 웹입니다.
                                    팀 프로젝트로 진행하였으며 프론트엔드를 맡았습니다.
                                    DB SQL문을 이해하는 기회가 되었습니다.
                                    
                                </div>
                                <div className="image-link t1">
                                    <div className="featured-post">
                        <a href="https://github.com/JaeseungJung/Sol/tree/main/exploror"> <img src="images/section/ethscan.png" alt="images" /></a>

                                    </div>

                                    <div className="text-center">
                            <h1 className="clip is-full-width title color-d2 mg-b31">
                                   블록체인 스캐너 </h1>
                                   개발기간: 3일
                                    </div>
                                    Web3.js를 이용해 EOA, 트랜잭션, 블록넘버를 조회하는 웹입니다.
                                    컨트랙트 개발에 앞서 블록체인 구조를 이해하는데 도움이 되었습니다.
                                    
                                </div>
                                {/* <div className="fl-btn text-center"> 
                                    <Link to="#" className="f-w500">Explore More</Link>
                                </div> */}
                                
                            </div>
                            <div className="flat-spacer" data-desktop={0} data-mobile={100} data-smobile={100} />
                            <div className="col-right animate-element wow delay5 fadeInUp" data-wow-delay="0.5s">
                                <div className="flat-title t2 mg-b60">
                                    <h4 className="sub-title mg-b13">Portfolio</h4>
                                    <h2 className="title-section color-d12 mg-b20">포트폴리오</h2>
                                    <p>
                                       교육을 이수하면서 만든 웹 포트폴리오 입니다.
                                    </p>
                                </div>
                                <div className="pd-right">
                                    <div className="image-link t1">
                                        <div className="featured-post">
                        <a href="https://github.com/JaeseungJung/Sol/tree/main/contract/quiz_Pang"> <img src="images/section/quiz.png" alt="images" /></a>

                                        </div>

                                        <div className="portfolio-template-link text-center">
                                        <h1 className="clip is-full-width title color-d2 mg-b31">
                                        퀴즈팡 - Dapp</h1>
                                        개발기간: 5일
                                        
                                    </div>
                                    스마트컨트랙트를 배우고 처음 만든 Dapp입니다.
                                    퀴즈를 풀어 정답을 맞추면 이더리움 보상을 얻습니다.
                                    프론트에서 퀴즈 답안을 제출 할때 msg.sender와 답안을 해싱하여
                                    답을 가로채지 못하게 설계했습니다.


                                    </div>
                                    <div className="image-link t1">
                                        <div className="featured-post">
                                        
                        <a href="https://github.com/UiCheonKim/BlockChain-Project/tree/main/nextree"> <img src="images/section/nextree.png" alt="images" /></a>

                                        </div>

                                        <div className="portfolio-template-link text-center">
                                        <h1 className="clip is-full-width title color-d2 mg-b31">
                                        넥스트리</h1>
                                        개발기간: 한달
                                    </div>
                                    다음 세대를 위해 나무를 심는 컨셉으로 만든 Dapp 입니다.
                                    팀프로젝트로, 스마트컨트랙트와 프론트엔드 디자인을 맡았습니다.
                                    기부한 이더 금액에 따라 ERC 1155로 발급된 나무 카드를 받습니다. 
                                    또한 ERC 1155 티켓을 사용하면 체인링크 VRF를 이용해 랜덤으로 아트 NFT를 얻을 수 있습니다.
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        );
    }
}

export default CaseStudy;
