import React, { Component } from 'react';
import {Link} from 'react-router-dom';



class Banner extends Component {
    render() {
        return (
            
            
            <section className="banner-section s3 two-home" id="home">
                
                <div className="container">
                    
                    <div className="content-text position-relative">
                        <div className="animate-element wow delay5 fadeInDown" data-wow-delay="0.3s">
                            <div className="tag color-d1 lt-sp53 mg-b24">smart contract developer</div>
                            <h1 className="cd-headline clip is-full-width title color-d2 mg-b31">
                                <span>안녕하세요, </span> <br/>
                                <span className="cd-words-wrapper color-d6">
                                <b className="is-visible">저는</b>
                                <b> 개발자 </b>
                                <b>정재승입니다.</b>
                                </span>
                            </h1>
                            <p className="lt-sp02 mg-b51">
                               탈중앙화 Dapp 생태계에 매력을 느껴 컨트랙트 개발자가 되기로 결심했습니다. <br />
                               사용자에게 친숙하고 안전한 환경을 만들어주는 개발자가 되겠습니다.
                            </p>
                        </div>
                        <div className="animate-element wow delay5 fadeInUp" data-wow-delay="0.3s">
                            <div className="fl-btn btn-general btn-hv-common color-s1">
                                <Link to="#portfolio" className="btn-inner f-w500 color-d6 border-corner5 lt-sp01"> 포트폴리오 </Link>
                            </div>
                        </div>
                    </div>
                    <div className="featured-post border-corner-bl">
                        
                        <img src="images/section/s_Banner3.png" alt="images" />
                        <div className="site-list has-border text-center animate-element wow delay5 fadeInUp" data-wow-delay="0.3s">
                        {/* <a href="https://github.com/JaeseungJung" className="bg-s1"><i className="fa fa-github" aria-hidden="true" /> </a> */}
                        {/* <a href="https://orbing.tistory.com/" className="bg-s1"><i className="fa fa-medium" aria-hidden="true" /> </a> */}

                        </div>
                    </div>
                    </div>
                    <div className="iconbox-s4 t2 color-s2 border-corner5 animate-element wow delay5 fadeZooming" data-wow-delay="0.3s">
                        <div className="avatar position-relative">
                            <img src="images/footer/01.png" alt="images" />
                        </div>
                        <div className="content-inner">
                            <h4 className="name lt-sp04 mg-b16 text-white position-relative">Jason Jung</h4>
                            <div className="fl-btn btn-general">
                            <a href="mailto:xiel0113@naver.com" className="text-white border-corner2">I am ready</a>  
                            </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Banner;
